.search {
    background-color: rgba(255, 255, 255, 0.931);
    color: #111;
    font-size: 2rem;
    border-radius: 10px; 
}

input {
    width: 400px;
    text-align: center;
    background-color: red;
}

.pa3 {
    margin-bottom: 2rem;
}

input::placeholder {
    color: rgba(17, 17, 17, 0.678);
}

input:focus::placeholder {
    color: transparent;
}

/* For mobile */

@media (max-width: 600px) {
    input.search {
      width: 350px;
      padding: 0.4rem;
    }
    input::placeholder {
        color: rgba(17, 17, 17, 0.466);
    }
  }