@import url(https://fonts.googleapis.com/css2?family=Goldman&display=swap);
body {
  margin: 0;
  font-family: 'Goldman', cursive;
  letter-spacing: 2px;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  min-height: 900px;
  background: url(/discgolf-in-the-bag/static/media/dg-bg.9d47e462.jpg);
  background-repeat: no-repeat;
  background-size: cover;
  background-attachment: fixed;
  background-position: center; 
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.card {
    min-width: 400px;
    box-shadow: inset 0 200000px rgb(0 0 0 / 85%);
}

.card h2, .card h1, .card p {
    color: #fff;
    text-shadow: 2px 2px 5px #111;
}

.card h1  {
    font-size: 3rem;
}

.card h2 {
    font-size: 2rem;
}

.card img {
    width: 250px;
    height: 250px;
}

.flight-numbers {
    background-color: rgba(80, 238, 40, 0.411);
    padding: 15px;
    font-size: 2rem;
    color: #fff;
}

p.flight-numbers {
    color: #fff;
    text-shadow: 2px 2px 5px #111;
}

.disc-type {
    font-size: 1.2rem;
}

/*For mobile*/

@media (max-width: 600px) {
    .card {
        min-width: 300px;
    }
    .card img {
        width: 150px;
        height: 150px;
    }
    .card h1  {
        font-size: 2rem;
    }
    
    .card h2 {
        font-size: 1rem;
    }
    .flight-numbers {
        font-size: 1rem;
    }
  }

.card-list {
    min-height: 900px;
}

/*For mobile*/

@media (max-width: 600px) {
    .card-list {
        min-height: 500px;
    }
}
.search {
    background-color: rgba(255, 255, 255, 0.931);
    color: #111;
    font-size: 2rem;
    border-radius: 10px; 
}

input {
    width: 400px;
    text-align: center;
    background-color: red;
}

.pa3 {
    margin-bottom: 2rem;
}

input::-webkit-input-placeholder {
    color: rgba(17, 17, 17, 0.678);
}

input:-ms-input-placeholder {
    color: rgba(17, 17, 17, 0.678);
}

input::placeholder {
    color: rgba(17, 17, 17, 0.678);
}

input:focus::-webkit-input-placeholder {
    color: transparent;
}

input:focus:-ms-input-placeholder {
    color: transparent;
}

input:focus::placeholder {
    color: transparent;
}

/* For mobile */

@media (max-width: 600px) {
    input.search {
      width: 350px;
      padding: 0.4rem;
    }
    input::-webkit-input-placeholder {
        color: rgba(17, 17, 17, 0.466);
    }
    input:-ms-input-placeholder {
        color: rgba(17, 17, 17, 0.466);
    }
    input::placeholder {
        color: rgba(17, 17, 17, 0.466);
    }
  }
.footer {
    background-color:  rgb(0 0 0 / 85%);
    padding: 1rem;
    margin: 1rem 1rem;     
}

.footer h1 {
    color: #fff;
    text-shadow: 2px 2px 5px #111; 
}
h1.app {
    font-size: 5rem;
    color: #fff;
    text-shadow: 2px 2px 5px #111;
}


/* For mobile */

@media (max-width: 600px) {
    h1.app {
      font-size: 3rem;
    }
  }
